import { Component } from '@angular/core';
import * as io from 'socket.io-client';
import { Validators, FormControl, FormGroup, FormArray, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-root',  
  templateUrl: './app.component.html',
  styles: []
})
export class AppComponent {
  socket;
  usuario = 'Fulano';
  mensagens = ['Maçã','Banana'];
  chatForm = this.fb.group({
    mensagem: []
  })

  constructor(private fb: FormBuilder) {
    this.socket = io();
  }
  
  enviarMensagem(modal) {
    var data = new Date();
    var dataHora = (data.toLocaleString());
    var mensagem = modal.mensagem;
    var msgs = this.mensagens;
    if(mensagem != null && mensagem != "") {
      
      this.socket.emit('chat message', this.usuario + ': ' + mensagem + ' ' + dataHora);
      this.chatForm.get('mensagem').setValue('');

      this.socket.on('chat message', function(msg) {
        msgs.push(msg);
      });

    }
    return false;
  }

  title = 'chatbox';
}
